export const cities = citiesRaw.map((city, index) => {
  
  const isUniqueCity = citiesRaw.filter(other => other.city === city.city).length === 1
  const label = isUniqueCity ? city.city : `${city.city} - ${city.state}` 
  return {
    label: label,
    lon: city.lon,
    lat: city.lat,
  }
}).sort((a, b) => a.label.localeCompare(b.label))

