import { CircularProgress } from "@mui/material"
import { useQuery } from "react-query"
import { useParams } from "react-router-dom"
import { PlainTextResponse } from "../../../../interfaces/httpResponses"
import { fetchAzureFunction } from "../azureFunctions"
import { SmartSnackBar } from "../components/SnackBarComponents"

export function Confirm() {
  const params = useParams()
  const uid = params.uid

  const confirmQuery = useQuery<PlainTextResponse, Error>('confirm',
    () => fetchAzureFunction(`confirm?uid=${uid}`),
    {
      retry: false,
      refetchOnWindowFocus: false
    })

  if (confirmQuery.isLoading) return (
    <div className="flex flex-col gap-2 items-center justify-center h-screen">
      <CircularProgress />
      confirming account
    </div>
  )
  return (
    <div>
      <SmartSnackBar
        severity="success"
        trigger={confirmQuery.isSuccess}
        message="confirmed"
      />
      <SmartSnackBar
        severity="error"
        trigger={confirmQuery.isError}
        message={confirmQuery.error?.message ?? ""} />
    </div>
  )
}
