import { Autocomplete, IconButton, MenuItem, Select, TextField } from "@mui/material"
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import React from "react";
import { LeafletMap } from "../LeafletMap";
import { cities } from "../assets/cities";

export type ItemPref = {
  location?: string
  radius: number
  lon?: number
  lat?: number
  key: string
}

interface ItemProps {
  itemKey: string
  setItemPref: (newItemPref: ItemPref) => void
}
function PrefItemComponent({ itemKey, setItemPref }: ItemProps) {

  const [location, setLocation] = React.useState<string>()
  const [radius, setRadius] = React.useState<number>(1)

  const city = cities.find(row => row.label === location)
  React.useEffect(() => {
    setItemPref({
      key: itemKey,
      location: location,
      radius: radius,
      lon: city?.lon,
      lat: city?.lat,
    })
  }, [location, radius])

  return (
    <div className="flex gap-2 justify-start w-full">

      <div className="w-48 max-w-full">
        <Autocomplete
          size="small"
          disablePortal
          options={cities}
          onChange={(e, value) => setLocation(value?.label)}
          renderInput={(params) => <TextField {...params} size="small" label="Plaats" />}
        />
      </div>
      <Select
        size="small"
        value={radius}
        label="Afstand"
        onChange={(e) => setRadius(e.target.value as number)}
      >
        <MenuItem value={1}>1 km</MenuItem>
        <MenuItem value={2}>2 km</MenuItem>
        <MenuItem value={3}>3 km</MenuItem>
        <MenuItem value={100}>100 km</MenuItem>
      </Select>

    </div>
  )
}

export const createUniqueKey = () => Math.random().toString(16).slice(2)
export const createEmptyItemPref = (): ItemPref => ({
  key: createUniqueKey(),
  location: undefined,
  radius: 1
})

const validatePref = (pref: ItemPref) => pref.location && pref.radius && pref.lon && pref.lat

interface Props {
  itemPrefs: ItemPref[]
  setItemPrefs: React.Dispatch<React.SetStateAction<ItemPref[]>>
}
export function PrefsComponent({itemPrefs, setItemPrefs}: Props) {

  const removeItemPref = (index: number) => {
    setItemPrefs(prev => {
      if (prev.length <= 1) return prev
      return prev.filter((_, i) => i !== index)
    })
  }

  return (
    <div className="flex flex-col">
      <div className="flex flex-col gap-2">
        {
          itemPrefs.map((itemPref, index) => {
            return <div className="flex" key={itemPref.key}>

              <IconButton style={{ borderRadius: 0 }} onClick={() => removeItemPref(index)}>
                <DeleteIcon />
              </IconButton>
              <PrefItemComponent itemKey={itemPref.key} setItemPref={
                (newItemPref) => setItemPrefs(prev =>
                  prev.map(otherItemPref => otherItemPref.key === itemPref.key ? newItemPref : otherItemPref))
              }
              />
            </div>
          })
        }
      </div>
      <div>
        <IconButton aria-label="delete"
          onClick={() => setItemPrefs(prev => [...prev, createEmptyItemPref()])}>
          <AddIcon fontSize="inherit" />
        </IconButton>
      </div>
      {/* <div className="w-full bg-blue-500 h-96">
        <LeafletMap itemPrefs={itemPrefs} />
      </div> */}
    </div>
  )
}