import { Button, CircularProgress, InputAdornment, LinearProgress, TextField } from "@mui/material";
import React from "react";
import { BrowserRouter, Outlet, Route, Routes, useParams } from "react-router-dom";
import { LeafletMap } from "./LeafletMap";
import { Edit } from "./pages/Edit";
import { PrefsComponent } from "./components/PrefsComponent";
import { Subscribe } from "./pages/Subscribe";
import { QueryClient, QueryClientProvider } from "react-query";
import { Test } from "./pages/Test";
import { Confirm } from "./pages/Confirm";

function Layout() {
  return (
    <div className="bg-white">
      {/* <Navbar />  */}
      <div className="flex justify-center">
        <Outlet />
      </div>
    </div>
  )
}
function Router() {
  return (
    <BrowserRouter >
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index path="/subscribe" element={<Subscribe />} />
          <Route index path="/test" element={<Test />} />
          <Route path="/edit/:uid" element={<Edit />} />
          <Route path="/confirm/:uid" element={<Confirm />} />
          <Route path="*" element={<>Page not found</>} />
        </Route>
      </Routes>
    </BrowserRouter>
  )
}

const queryClient = new QueryClient()
function App() {
  return <>
    <QueryClientProvider client={queryClient}>
      <Router />
    </QueryClientProvider>
  </>
}

export default App;
