import { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { createUniqueKey } from "../components/PrefsComponent"
import { useMutation, useQuery, useQueryClient } from "react-query";
import { AccountSuccessResponse } from "../../../../interfaces/httpResponses"
import { PrefBody } from "../../../../interfaces/httpBodies"
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { cities } from "../assets/cities";
import { useNavigate } from "react-router-dom";

import { Alert, Autocomplete, Button, CircularProgress, IconButton, MenuItem, Select, TextField } from "@mui/material";
import { fetchAzureFunction, postAzureFunction } from "../azureFunctions";
import { SmartSnackBar } from "../components/SnackBarComponents";
interface PrefItem {
  uid: string
  radius: number
  location: string
  lon: number
  lat: number
}

interface RowProps {
  prefItem: PrefItem,
  onRemove: () => void,
  setPrefItem: (fn: (prev: PrefItem) => PrefItem) => void
}
function Row({ prefItem, setPrefItem, onRemove }: RowProps) {


  const radius = prefItem.radius
  const setRadius = (radius: number) => setPrefItem(prev => ({ ...prev, radius: radius }))

  const currentCity = cities.find(row => row.label === prefItem.location) ?? null
  const setLocation = (location: string) => setPrefItem(prev => {
    return {
      ...prev,
      location: location,
      lon: currentCity?.lon ?? 0,
      lat: currentCity?.lat ?? 0,
    }
  })

  return (
    <div className="flex gap-1">
      <IconButton style={{ borderRadius: 0 }} onClick={onRemove}>
        <DeleteIcon />
      </IconButton>
      <div className="w-48 max-w-full">
        <Autocomplete
          disablePortal
          options={cities}
          value={currentCity}
          onChange={(e, newValue) => setLocation(newValue?.label ?? "")}
          // onInputChange={(_, newInputValue) => setLocation(newInputValue)}
          renderInput={(params) => <TextField {...params} label="Plaats" />}
        />
      </div>
      {/* <TextField
        value={prefItem.location}
        onChange={(e) => setPrefItem(prev => ({ ...prev, location: e.target.value }))}
      /> */}
      <Select
        sx={{ width: 1 / 3 }}
        value={radius}
        label="Afstand"
        onChange={(e) => setRadius(e.target.value as number)}
      >
        <MenuItem value={1}>1 km</MenuItem>
        <MenuItem value={2}>2 km</MenuItem>
        <MenuItem value={3}>3 km</MenuItem>
        <MenuItem value={100}>100 km</MenuItem>
        <MenuItem value={200}>200 km</MenuItem>
      </Select>
      {/* ({prefItem.lon},{prefItem.lat}) */}
    </div>
  )
}

export function Edit() {
  const queryClient = useQueryClient()

  const params = useParams()
  const uid = params.uid

  const [prefItems, setPrefItems] = useState<PrefItem[]>()

  const { isLoading: accountIsLoading, error: accountError, data: account } = useQuery<AccountSuccessResponse, Error>('account',
    () => fetchAzureFunction(`account?uid=${uid}`),
    {
      onSuccess: (account) => setPrefItems(account.prefs.map(pref => ({
        uid: createUniqueKey(),
        lat: pref.lat,
        lon: pref.lon,
        radius: pref.radius,
        location: pref.location
      }))),
      retry: false,
      refetchOnWindowFocus: false
    })
  const prefMutation = useMutation(
    async (body: PrefBody) => postAzureFunction<PrefBody>('prefs', body),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['account'])
      },
      onMutate: (x) => console.log('succesfully mutated!', x),
    }
  )

  const navigate = useNavigate()
  const unsubQuery = useQuery<string, Error>('unsub',
    () => fetchAzureFunction(`unsub?uid=${uid}`),
    {
      onSuccess: async () => {
        await new Promise(res => setTimeout(res, 3_000))
        await navigate('/subscribe')
      },
      retry: false,
      refetchOnWindowFocus: false,
      enabled: false
    }
  )


  const handleClick = () => {
    if (!account || !prefItems) return
    const body: PrefBody = {
      uid: account.uid,
      prefs: prefItems!,
    }
    prefMutation.mutate(body)
  }

  if (accountIsLoading) return <div className="flex items-center justify-center h-screen"> <CircularProgress /> </div>
  if (accountError) return (<div> {accountError.message} </div>)
  if (!account) return (<div> no account? </div>)

  const addToPrefItems = (item: PrefItem) => {
    setPrefItems(prevs => {
      if (!prevs) return prevs
      if (prevs.length >= 3) return prevs
      return [...prevs, item]
    })
  }
  const addNewItemPref = () => addToPrefItems(
    {
      location: "Amsterdam",
      lon: 4.897070,
      lat: 52.377956,
      radius: 1,
      uid: createUniqueKey()
    })

  const removeFromPrefItems = (index: number) => {
    setPrefItems(prevs => {
      if (!prevs) return prevs
      if (prevs.length <= 1) return prevs
      return prevs.filter((_, i) => i !== index)
    })
  }

  return (
    <div className="flex flex-col mt-20 gap-2">
      <div className="text-xl font-bold text-center"> Wijzig uw instellingen </div>
      <div className="text-xl font-bold underline text-blue-500 text-center mb-5"> (+{account.phonenumber}) </div>
      <div className="flex flex-col gap-2">
        {prefItems && prefItems.map((item, index) => {
          return (<div key={item.uid}>
            <Row
              onRemove={() => removeFromPrefItems(index)}
              prefItem={item}
              setPrefItem={
                (fn: (prev: PrefItem) => PrefItem) => {
                  setPrefItems(prevs => prevs?.map((prev, i) => i === index ? fn(prev) : prev))
                }
              }
            />
          </div>
          )
        }
        )
        }
      </div>
      <div className="justify-start">
        <IconButton onClick={addNewItemPref}>
          <AddIcon />
        </IconButton>
      </div>
      <Button variant="contained" color="primary"
        onClick={handleClick}
      >
        wijzig
      </Button>

      <SmartSnackBar
        severity="success"
        trigger={prefMutation.isSuccess}
        message="Instellingen gewijzigd"
        autoHideDuration={3_000}
      />
      <SmartSnackBar
        severity="error"
        trigger={prefMutation.isError}
        message={prefMutation.isError ? ((prefMutation.error as any) as Error).message : ""}
        autoHideDuration={3_000}
      />

      <div className="flex justify-center">
        {prefMutation.isLoading && <CircularProgress />}
      </div>
      <Button variant="contained" color="error"
        onClick={() => unsubQuery.refetch()}
      >
        Verwijder account
      </Button>
      <div className="flex justify-center">
        {unsubQuery.isLoading && <div> <CircularProgress /> </div>}
        {unsubQuery.isError && <div> {((prefMutation.error as any) as Error).message}</div>}
        {unsubQuery.isSuccess && <Alert severity="success"> Account verwijderd </Alert>}
      </div>
    </div>
  )
}
