const API_URL = "https://huizenalert-app-eu.azurewebsites.net/api";

export async function fetchAzureFunction<Response>(
  route: string
): Promise<Response> {
  const url = `${API_URL}/${route}`;
  const response = await fetch(url);
  if (!response.ok) {
    throw Error(await response.text());
  }
  let data: string = await response.text();
  try {
    return JSON.parse(data) as Response;
  } catch {
    return data as Response;
  }
}

export async function postAzureFunction<Body>(
  route: string,
  body: Body
): Promise<void> {
  const url = `${API_URL}/${route}`;
  const response = await fetch(url, {
    method: "POST",
    body: JSON.stringify(body),
  });
  if (!response.ok) {
    throw Error(await response.text());
  }
}
