import { useState } from "react"

interface Objeto {
  name: string
}
export function Test() {

  const [list, setList] = useState<Objeto[]>([])

  const addToList = (item: Objeto) => {
    setList(prevs => [...prevs, item])
  }

  const removeFromList = (index: number) => {
    setList(prevs => {
      if(prevs.length <= 1) return prevs 
      return prevs.filter((_, i) => i !== index)
    })
  }
  return (
    <div className="flex flex-col">
      <div>
        {list.map((item, index) => {

          return ( <div key={`row-${index}`}>
          <button key={`btn-${index}`} onClick={() => removeFromList(index)}> remove </button>
          <div key={`div-${index}`}> {item.name} </div>
          </div>
          )
        }
        )
        }
      </div>
      <button onClick={() => addToList({ name: new Date().toISOString() })}> add </button>
    </div>
  )
}